@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/library-react-app/static/media/janko-ferlic-sfL_QOnmy00-unsplash.a5e3913c.jpg) no-repeat center center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Nunito Sans', sans-serif;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1 {
  color: white;
  text-shadow: 0 0 30px black;
}

h2 {
  margin: 0
}

#bookShelf {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  margin: 30px;   
  margin: auto;
  width: 900px;
}

#emptyMsg {
  color: white;
  text-shadow: 0 0 10px black;
  font-size: 20px;
  margin: 2.5em;
}

.bookCard {
  display: inline-block;
  text-align: left;
  padding: 1em;
  background-color:rgba(255,255,255,1);
  box-shadow: 0 0 10px black;
  border-radius: 0.6em;
  position: relative;
  height: 160px;
}

div#darkenedBackground {
  background-color: rgba(0,0,0,0.4);
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto; /* Enable scroll if needed */
}

form {
  z-index: 3;
  position: fixed;
  text-align: left;
  top: 100px;
  left: 0;
  right: 0;

  font-size: 16px;
  margin:auto;
  background-color: rgba(255,255,255,1);
  padding: 20px;
  border-radius: 0.6em;
  box-shadow: 0 0 15px rgba(0,0,0,0.5);
  width: 350px;

  /*
  display: none;
  margin-left: auto;
  margin-right: auto;
  border-radius: 6px;
  width: 350px;
  padding: 20px;
  background-color: white;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);*/
}

form input[type='text'] {
  float: right;
}

.title {
  font-size: 20px;
  font-weight: bold;
  margin-top: 0;
}

label, input, p{
  font-size: 14px;
}

button {
  font-size:15px;
  border: none;
  margin-top: 5px;
  padding: 8px 16px;
  border-radius: 0.6em;
  font-family: 'Nunito Sans', sans-serif;
}

button:hover {
  cursor: pointer;
}

button#showForm {
  background-color: green;
  color: white;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 15px;
  margin-top: 50px;
  box-shadow: 0 0 15px rgba(0,0,0,0.5);
}

button.delete {
  background-color: rgb(189, 33, 33);
  position: absolute;
  right: 1em;
  bottom: 1em;
  color: white;
}

button.delete:hover {
  background-color: rgb(209, 58, 58);
  color: white;
}

button#addBook {
  background-color: green;
  color: white;
}

button#addBook:hover {
  background-color: rgb(28, 151, 28);
  color: white;
}

button#read {
  position: absolute;
  bottom: 1em;
  background-color: grey;
  color: white;
}

button#cancelAdd {
  background-color: grey;
  color: white;
}

button#read:hover {
  background-color:rgb(151, 150, 150);
}

button#cancelAdd:hover {
  background-color:rgb(151, 150, 150);
}

